body{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: black;
  color: hotpink;
  place-items: center;
}
#myBox{
  background-color:#ffcff1;
  opacity: 0.85;
  display: flex;
  color: #000;
  font-size: 3rem;
  font-weight: bolder;
  height: 350px;
  width: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 20px;
  transition: smooth;       
  margin-bottom: 20px;
}
button{
  font-weight: bolder;
  font-size: 1.2rem;
  border-radius: 10px;
  padding: 10px 30px;
  background-color: hsl(0, 0%, 100%);
  color: #fb928e;
  border: none;
  cursor: pointer;
  text-align: center;
}